/*
=========================================
  Slick Slider -> Конфигурация
========================================= */
$(function() {
  const sliderParams = {
    mobileFirst: true, // from low resolutions
    infinite: true,
    dots: false,
    arrows: true,
    prevArrow: '<button class="slider__arrow  slider__arrow--prev  btn"></button>',
    nextArrow: '<button class="slider__arrow  slider__arrow--next  btn"></button>',
    customPaging: function(slider, i) {
      return '<button class="slider__dot" type="button"></button>';
    },
    /*
    responsive: [
      {
        breakpoint: 830,
        settings: {
          arrows: false,
          // settings: "unslick",
        }
      },
    ],
    */
  };


  /* Cлайдер программ
  ========================================= */
  const sliderThumbParams = {
    mobileFirst: true, // from low resolutions
    infinite: true,
    // dots: false, // default false
    arrows: false,
    slidesToShow: 4,
    slidesToScroll: 1,
    // currentSlide: 2,
    focusOnSelect: true,
    // centerMode: true,
  };

  const sliderThumbParamsResp = {
    responsive: [
      {breakpoint: 1817, settings: {slidesToShow: 6}}, // Сработает при 1818 (mobileFirst: true)
      {breakpoint: 991, settings: {slidesToShow: 5}},
      {breakpoint: 767, settings: {slidesToShow: 4}},
    ],
  };


  /* Cлайдер аниматоров
  ========================================= */
  const sliderAnimatorsParamsResp = {
    responsive: [
      // default: 768+ — 3 слайда
      {breakpoint: 768, settings: {slidesToShow: 2, slidesToScroll: 2, dots: false}}, // 767- (mobileFirst: false) — 2 слайда
      {breakpoint: 576, settings: {slidesToShow: 1, slidesToScroll: 1, dots: false}}, // 575- (mobileFirst: false) — 1 слайд
    ],
  };


  // FLOW
  const $programsSlider = $('#programs__slider');
  $($programsSlider).slick(Object.assign(sliderParams, {asNavFor: '#programs__thumbs-slider'}));

  // Миниатюры: слайдер программ
  function programsThumbsSliderInit() {
    $($programsThumbsSlider).slick(Object.assign(sliderThumbParams, {asNavFor: '#programs__slider'}, sliderThumbParamsResp));
  }
  const $programsThumbsSlider = $('#programs__thumbs-slider');
  programsThumbsSliderInit();


  /* ПОЗИЦИОНИРОВАНИЕ КНОПОК ВПЕРЕД/НАЗАД
   * — вынос за пределы контейнера
  ========================================= */
  const ARROW_CLASS_JS = 'slider__arrow--js-position';
  const ARROW_BODY_GAP = 20;
  const CONTAINER_GUTTER = 30; // padding

  function changeBtnPosition() {
    const btn = $('.slider__arrow--prev', $programsSlider).width() || null;
    const container = $($programsSlider).parents('.container').width() + CONTAINER_GUTTER || null;
    const body = $('body').width() || null;

    $('.slider__arrow', $programsSlider).removeClass(ARROW_CLASS_JS);
    if (!btn || !container || !body) { return; }

    if ((body - container) / 2 > (btn + ARROW_BODY_GAP)) {
      $('.slider__arrow', $programsSlider).addClass(ARROW_CLASS_JS);
    }
  }

  changeBtnPosition();


  /* Resize
  ========================================= */
  let t0;
  $(window).on('resize', function() {
    clearTimeout(t0);
    t0 = setTimeout(function() {
      // Стрелки
      changeBtnPosition();

      // Миниатюры: слайдер программ
      const isSlickOn = $($programsThumbsSlider).hasClass('slick-initialized');
      if (lib.isMobile && isSlickOn) {
        $($programsThumbsSlider).slick('unslick');
      } else if (!lib.isMobile && isSlickOn === false) {
        programsThumbsSliderInit();
      }
    }, 300);
  });


  /* Инициализация
  ========================================= */
  // yt-видео
  $('#dream__slider').slick(Object.assign(sliderParams, {dots: true}));
  // аниматоры
  $('#animators__slider').slick(Object.assign(sliderParams, sliderAnimatorsParamsResp, {dots: true, slidesToShow: 3, slidesToScroll: 3, mobileFirst: false}));
});
