require('imports-loader?this=>window!wowjs/dist/wow.min'); // eslint-disable-line
const Parallax = require("parallax-js/dist/parallax.min.js");

$(function() {
  /*
  =========================================
    ДЕАКТИВИРУЕТ TEL-ССЫЛКИ НА ДЕСКТОПАХ
  ========================================= */
  const telLinks = document.querySelectorAll('a[href^="tel:"]');
  let isTelLinksDisabled = false;

  const disableTelLinks = function() {
    telLinks.forEach(function(el, idx) {
      if (!el.dataset.href) {
        el.dataset.href = el.getAttribute('href');
      }
      el.removeAttribute('href');
    });
    isTelLinksDisabled = true;
  };

  const enableTelLinks = function() {
    telLinks.forEach(function(el, idx) {
      el.setAttribute('href', el.dataset.href);
    });
    isTelLinksDisabled = false;
  };


  // FLOW
  if (!window.lib.isMobile) {
    disableTelLinks();
  }

  // Обработчик на ресайз окна
  $(window).on('resize', function() {
    if (isTelLinksDisabled === true && window.lib.isMobile === true) {
      // ВКЛ.
      enableTelLinks();
    } else if (isTelLinksDisabled === false && window.lib.isMobile === false) {
      // ОТКЛ.
      disableTelLinks();
    }
  });


  /*
  =========================================
    ПЛАВНАЯ ЯКОРНАЯ НАВИГАЦИЯ
  ========================================= */
  $("body").on('click', "a[href^='#']:not([data-toggle='modal'])", function(evt) {
    if (!this.hash || !$(this.hash).length) { return; }

    evt.preventDefault();
    const shiftTop = 50;
    $("html,body").animate({scrollTop: $(this.hash).offset().top - shiftTop}, 1000);

    // Если Моб.меню: Закрыть мод.окно (после клика в меню)
    const modalMainNav = $(this).parents('#modal--main-nav');
    if (modalMainNav.length > 0 && lib.isTabletDown === true) {
      $(modalMainNav).modal('hide');
    }
  });

  /*
  =========================================
    Parallax
  ========================================= */
  (() => {
    /* eslint-disable no-new */
    // header
    new Parallax(document.getElementById('scene-circles'));
    new Parallax(document.getElementById('scene-waves-top'));
    if (lib.isMobile === false) { new Parallax(document.getElementById('scene-waves-bottom')); }
    new Parallax(document.getElementById('scene-persons'));
    // 2nd screen
    if (lib.isMobile === false) { new Parallax(document.getElementById('scene-promo')); }
    // dream
    if (lib.isMobile === false) { new Parallax(document.getElementById('scene-pencil')); }
  })();


  /*
  =========================================
    Animations: Wow + Animate.css
  ========================================= */
  $('.promo__column-inner').addClass('wow fadeInLeftBig').attr('data-wow-offset', -150).attr('data-wow-duration', '0.8s');
  $('.promo__callback').addClass('wow fadeInUp').attr('data-wow-delay', '0.2s');
  // $('.programs__title, .programs__descr').addClass('wow fadeInRight');
  $('.programs__column').addClass('wow fadeInLeft').attr('data-wow-delay', '0.2s');
  $('.services__title, .services__descr').addClass('wow fadeInUp');
  $('.dream__title, .dream__descr').addClass('wow fadeInRight');
  $('.dream__content').addClass('wow fadeInLeft').attr('data-wow-delay', '0.2s');
  $('.animators__title').addClass('wow fadeInUp');
  $('.place__like').addClass('wow flipInY');
  // lg+
  if($(window).width() + 15 > 992) {
    $('.footer__girl--left').addClass('wow fadeInLeft');
    $('.footer__girl--right').addClass('wow fadeInRight').attr('data-wow-delay', '0.5s');
  }

  new WOW({ // eslint-disable-line no-undef
    boxClass: 'wow', // animated element css class (default is wow)
    animateClass: 'animated', // animation css class (default is animated)
    offset: 0, // distance to the element when triggering the animation (default is 0)
    mobile: true, // trigger animations on mobile devices (default is true)
    live: true, // act on asynchronously loaded content (default is true)
  }).init();
});
