/* Кнопка «Наверх»
========================================= */
/*!
  ЛОГИКА
  Проверка: есть ли у страницы вертикальный скролл?
    [ДА] ->
      Покажем кнопку если:
        — не в первом экране (по умолчанию).
      Скроем кнопку если:
        — в первом экране.
*/

$(function() {
  if (!lib.isVerticalScrollPresent()) { return; }

  // Создание кнопки
  const $topBtn = $('<div class="go-top__btn" title="Наверх"></div>')
    .appendTo('body')
    .hide()
    .on('click', function() { $('html, body').animate({scrollTop: 0}, 300); });
  let isVisible = false;


  function setVisibility() {
    const isFirstScreen = lib.isFirstScreen(0.33); // 33% окна прозапас
    // Скрыть
    if (isFirstScreen && isVisible === true) {
      $($topBtn).fadeOut();
      isVisible = false;
    // Показать
    } else if (!isFirstScreen && isVisible === false) {
      $($topBtn).fadeIn();
      isVisible = true;
    }
  }

  let timeoutID;
  $(window).on('scroll', function() {
    clearTimeout(timeoutID);
    timeoutID = setTimeout(setVisibility, 100);
  });
});
